@import url(https://fonts.googleapis.com/css?family=Open+Sans:300, 400);
body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  background: rgba(0, 0, 0, 0.05);
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.post {
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.15);
  width: 350px;
  height: 350px;
  flex: 0 0 350px;
}

.post .description {
  flex: 1 1;
  padding-left: 25px;
  font-size: 25px;
}

.detail {
  width: 600px;
  height: 600px;
  flex: 0 0 600px;
}

.detail .description {
  flex: 1 1;
  padding-left: 40px;
  font-size: 38px;
}

.title {
  font-size: 42px;
}

.content {
  font-size: 18px;
}

.new-post {
  background-color: rgba(0, 0, 0, 0.04);
}

.close {
  opacity: 0.5;
  margin-top: 60px;
  margin-right: 60px;
}

.close img {
  width: 20px;
  height: 20px;
}

.card {
    height: 200px;
    width: 200px;
    padding: 15px;
    margin: 15px;
    background: white;
    box-shadow: 0 2px 3px rgba(0,0,0,0.3);
}

.card-btn-one {

}





.nametag {
    background: #61dafb;
    color: white;
    padding: 0.3rem;
}
.reply {

}


.textbubbles {
    border-radius: 10px;
    font-weight: lighter;
    margin: 0.5rem;
}

.textbubbles > span {
    border-radius: 10px;
    padding: 0.2rem;
    font-size: 1.2rem;
    font-weight: lighter;
}
.textbubbles > div {
    border-radius: 10px;
    padding: 0.2rem;
    font-size: 1.2rem;
    font-weight: lighter;
}

.reviewtitle {
    background: lightblue;
    font-size: 1.5rem;
    font-weight: normal;
    margin-left: 0.5rem;
}
.targettext {
    background: lightgoldenrodyellow;
    font-size: 1.5rem;
    font-weight: normal;
    margin-left: 0.5rem;
}

.reviewtext {
    background: #e2dede;
}
.comment {
    margin: 0.3rem;
    margin-left: 4rem;
    font-size: 1.1rem;
    /*display: flex;*/
    /*flex-direction: row;*/
}
.reply {

    margin-left: 6rem;
    font-size: 1.1rem;
}

.comment-btn {

}
nav {
  display:flex;
  position:relative;
  z-index: 10;
  flex-direction: row-reverse;
  box-shadow:0 3px 4px rgba(0, 0, 0, 0.3);
  min-width: 100%;
  background: black;
  /*margin-bottom: 5vw;*/
}

nav > * {
  border-right: solid lightyellow 4px;
  padding:0.6em;
  text-decoration: none;
  font-weight: bold;
  color:white;
}

a {
  text-decoration: none;
  /*color:white;*/
}

a:visited {
  /*color:white;*/
}

a:hover {
  /*transition-duration: 0.5s;*/
  background-color: #ff8b40;
  /*color:white;*/
}

@media (max-width: 500px) {
  nav {
    display: flex;
    flex-grow: 1;
    flex-direction: row;
    justify-content: center;
  }
}

